"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
class DataStorageManager {
    constructor() {
        this.data = []; //  array
    }
    static getInstance() {
        if (!DataStorageManager.instance) {
            DataStorageManager.instance = new DataStorageManager();
        }
        return DataStorageManager.instance;
    }
    updateData(id, newData) {
        const index = this.data.findIndex((item) => item.id === id);
        if (index !== -1) {
            this.data[index] = newData;
            console.log("Data updated:", newData);
        }
        else {
            this.data.push(newData);
            console.log("Data added:", newData);
        }
    }
    collectDataFromDOM() {
        const elements = document.querySelectorAll('[data-store]');
        elements.forEach((element) => {
            const id = element.id;
            const value = element.innerHTML.replace(/<br\s*\/?>/gi, '\n'); //  <br>  \n
            // const value = element.textContent || "";
            this.updateData(id, { id, value });
        });
        console.log("Data collected from DOM:", this.data);
    }
    applyDataToDOM() {
        const elements = document.querySelectorAll('[data-store]'); //  data-store 
        elements.forEach((element) => {
            const id = element.id; // DOM element  id
            const dataItem = this.data.find((item) => item.id === id); //  data  id 
            if (dataItem) {
                element.innerHTML = dataItem.value.replace(/\n/g, '<br>'); //  \n  <br>
                //  data-store="sheetblock"  blur 
                if (element.getAttribute("data-store") === "sheetblock") {
                    element.dispatchEvent(new Event("blur")); //  blur 
                }
                // element.textContent = dataItem.value; //  data  value  DOM 
                console.log(`Element with id "${id}" updated with value: "${dataItem.value}"`);
                // formatText(htmlElement);
            }
            else {
                console.warn(`No matching data found for element with id: ${id}`);
            }
        });
    }
    // for download 
    getData() {
        this.collectDataFromDOM();
        return this.data;
    }
    clearData() {
        this.data = [];
        console.log("Data has been cleared.");
    }
}
exports.default = DataStorageManager.getInstance();
